.login-span {
  width: 100%;
  height: 100%;
  position: absolute;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border: 5px solid #f8cacc;
  font-family: 'Source Sans Pro', sans-serif;
  line-height: 50px;
  font-size: 12pt;
  text-align: center;
  text-transform: uppercase; }

.login-span:nth-child(1) {
  color: #8895c3;
