.avatar-flip {
  border-radius: 150px;
  overflow: hidden;
  height: 150px;
  width: 119px;
  position: relative;
  margin: auto;
  top: -30px;
  box-shadow: 0 0 0 13px #bbc2dd;
  -webkit-box-shadow: 0 0 0 13px #bbc2dd;
  -moz-box-shadow: 0 0 0 13px #bbc2dd; }

.avatar-flip img {
  position: absolute;
  left: 0;
  top: 0;
  border-radius: 10px;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out; }

.avatar-flip img:first-child {
  z-index: 1; }

.avatar-flip img:last-child {
  z-index: 0;
  transform: rotateY(180deg);
  -webkit-transform: rotateY(180deg);
  opacity: 0; }

